    height: 30px;
    margin: 10px 30px 0;
    -webkit-transform: rotate(-30deg) skew(25deg);
            transform: rotate(-30deg) skew(25deg);
    background: #ccc;
}

.icons ul li span {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #000;
    transition: 0.5s;
    display: -webkit-flex !important;
    display: flex !important;
    -webkit-align-items: center;
