    opacity: 0.4;
}

.icons ul li:hover span:nth-child(1) {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0);
    opacity: 0.2;
}

.icons ul li:nth-child(1) span {
    background: #dd4b39;
