    padding-top: 90px;
    padding-bottom: 60px;
}

#portfolio h1 {
    font: 18px/24px 'Poppins', sans-serif;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-align: center;
    margin-bottom: 48px;
    color: var(--color-neutral-lighest);
}

/* Portfolio Content */
#portfolio-wrapper .columns {
    margin-bottom: 36px;
}

.portfolio-item .item-wrap {
    background: var(--color-neutral-medium);
    overflow: hidden;
    position: relative;
    border-radius: 10px;
    color: var(--color-neutral-lighest);
    transition: all 0.3s ease-in-out;

    width: 230px;
    height: 150px;
}

.portfolio-item .item-wrap a {
    display: block;
    cursor: pointer !important;
}

.portfolio-item .category {
    display: none;
}

.portfolio-item .item-wrap img {
    vertical-align: bottom;
    width: 100%;
    height: 80%;
    object-fit: cover;
    cursor: pointer;
}

/* on hover */
.portfolio-item:hover .category {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    position: absolute;

    background-color: var(--color-neutral-medium);
    opacity: 0.9;

    width: 100%;
    height: 100%;
}

@media only screen and (max-width: 767px) {
    #portfolio-wrapper .columns {
      margin-bottom: 40px;
    }
    .popup-modal {
      max-width: 85%;
    }

    .flex-control-nav {
      text-align: center;
      margin-left: -30px;
    }
  
    #portfolio-wrapper {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
    }
  }
  
  @media only screen and (max-width: 480px) {
  
    #portfolio-wrapper {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
    }
  }
  
